<template>
  <div class="login">
    <van-nav-bar title="登录" left-arrow @click-left="$router.go(-1)" />

    <div class="container">
      <div class="title">
        <h3>用户登录</h3>
        <p>未注册请进行注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input
            class="inp"
            maxlength="11"
            placeholder="请输入用户名"
            type="text"
            v-model="username"
          />
        </div>
        <div class="form-item">
          <input
            class="inp"
            maxlength="11"
            placeholder="请输入密码"
            type="password"
            v-model="password"
          />
        </div>
      </div>

      <div class="login-btn" @click="admin_login">登录</div>
    </div>
  </div>
</template>

<script>
import { loginAPI } from '@/api/api'
export default {
  name: 'LoginPage',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    async admin_login () {
      const res = await loginAPI({
        username: this.username,
        password: this.password
      })
      console.log(res)
      console.log(res.status)
      if (res.status === 0) {
        localStorage.setItem('user_token', res.token)
        this.$router.push('/home')
      } else {
        alert('账号或密码错误，请重新输入')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg, #ecb53c, #ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
